<template>
  <div  class="input-box">
    <label @click="clickLabel">
      <img :style="{height: `${(fontSize+24)/75}rem`}" :src="icon" alt="" class="icon">
      <input type="text"
             ref="input"
             :style="{height: `${(fontSize+24)/75}rem`, fontSize: `${(fontSize)/75}rem`}"
             :value="value"
             :placeholder="placeholder"
             @focus="goToView"
             @blur="blur"
             @change="inputChange">

    </label>
    <span class="clear"
          v-if="hasClear" @click="clear">x</span>
  </div>

</template>

<script>

  export default {
    name: "input-m",
    props: {
      value: {
        type: [String, Number],
        default: ''
      },
      icon: {
        type: String,
      },
      placeholder: {
        type: String,
        default: ''
      },
      fontSize: {
        type: Number,
        default: 22
      },
      hasClear: {
        type: Boolean,
        default: false
      },

    },
    methods: {
      inputChange(e) {
        this.$emit('input', e.target.value.trim())
      },
      clear() {
        this.$emit('input', '')
      },
      goToView(e) {
        if(navigator.userAgent.indexOf('iPhone') !== -1) return
        setTimeout(() => {
          e.target.scrollIntoView(false)
        },400)
      },
      clickLabel(e) {
        this.$refs.input.focus()
        setTimeout(() => {
          this.$refs.input.focus()
          console.log(this.$refs.input)
        }, 100)
      },
      blur() {
        setTimeout(function() {
          document.documentElement.scrollBy(0 , -1);
          document.body.scrollBy(0 , -1);
          let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
          window.scrollTo(0, Math.max(scrollHeight - 1, 0));
        }, 200);
      }
    },
  }
</script>

<style scoped lang="scss">
.input-box {
  min-height: 82px;
  min-width: 300px;
  display: flex;
  padding: 0 60px 0 0;
  position: relative;
  img {
    width: auto;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  label {
    flex: 1;
    width: 100%;
    padding-left: 60px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    input {
      border: none;
      outline: none;
      width: 100%;
      text-indent: 30px;
      color: #333;
      position: relative;
      top: 2px;
    }
  }
  .clear {
    font-size: 34px;
    color: #666;
    position: absolute;
    top: 50%;
    right: 30px;
    transform: translateY(-50%);
  }
}
</style>
