<template>
  <div class="address-wrap">
    <div class="top-prize flex-between">
      <div class="left-desc">
        <span class="desc-tip">恭喜您获得</span>
        <div class="prize-name">{{prizeInfo.ticketName}}</div>
      </div>
      <div class="right-img flex">
        <img v-if="prizeInfo.ticketImg"
             :src="img_baseUrl+prizeInfo.ticketImg" alt="">
      </div>
    </div>

    <FormBox :type="type"
             :hasGet="hasGet"
             :showData="showData"
             @finish="getInputData"/>

    <div class="get-success" v-if="getSuccess">
      <img src="./imgs/get-success.png" alt="">
      <ButtonM class="btn-ack" @click="iKown">我知道了</ButtonM>
    </div>
  </div>
</template>

<script>
  import FormBox from './conponents/FormBox/FormBox';
  import ButtonM from '../../components/ButtonM/ButtonM';
  import {getPrize} from "../../api/my";
  import {title_tip} from "../../utils/publictip";
  import {baseUrl} from "../../utils/request";
  import {clog} from "../../api/punchin";
  const TIPS = {
    0: '领取成功', 1: '验证码失效', 2: '验证码错误', 3: '失败', 4: '兑奖截止时间已过期', 5: '已领取，请勿重复领取',
  }
  export default {
    name: "address-input",
    components: {FormBox, ButtonM},
    data() {
      return {
        type: '',
        showData: null,
        hasGet: false,
        getSuccess: false,
        prizeInfo: {},
        img_baseUrl: baseUrl.imgURL
      }
    },
    methods: {
      init() {
        this.prizeInfo = this.$route.params
        if(!this.prizeInfo.type) {
          this.$router.replace('/my-prize')
          return
        }
        if(this.prizeInfo.type==='check') {
          this.hasGet = true
          this.showData = this.reTransFormKey(this.prizeInfo)
        }
        this.type = this.prizeInfo.prizeType
        // if(this.prizeInfo.grandPrizeType.substr(0,1) === 'P' || this.prizeInfo.grandPrizeType.substr(0,1) === 'p') {
        //   this.type = 'real'
        // } else {
        //   this.type = 'virtual'
        // }
      },
      // 接收formBox组件传来的数据并提交
      getInputData(data) {
        console.log(data);
        const sendData = {
          infoKey: this.prizeInfo.infoKey,
          idcard: 'idcard',
          ...this.transFormKey(data)
        }
        getPrize(sendData).then(({result}) => {
          if(result.businessCode === '0') {
            this.getSuccess = true
            this.hasGet = true
          } else {
            title_tip('提 示', TIPS[result.businessCode], '我知道了');
          }
        })
        clog('天天抽奖', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'领取奖品','填完信息领取奖品']);
      },
      iKown() {
        this.getSuccess = false
        setTimeout(() => {
          this.navigateToV()
        }, 200)
      },
      // 讲formBox组件获取的信息对应到相应的字段
      transFormKey(data) {
        const sendData = {}
        const keyTransform = {
          name: 'userName',
          phone: 'phoneNum',
          address: 'address',
          street: 'street',
          validate: 'captcha',
        }
        for(const key in data) {
          const tranfKey = keyTransform[key]
          sendData[tranfKey] = data[key]
        }
        if(sendData.address) sendData.address = sendData.address + '-' +sendData.street
        return sendData
      },
      // 将后台获取的信息对应到formBox组件中
      reTransFormKey(target) {
        const data = {}
        const keyTransform = {
          name: 'userName',
          phone: 'phoneNum',
          address: 'address',
          street: 'street',
          validate: 'captcha',
        }
        for(const key in keyTransform) {
          const tranfKey = keyTransform[key]
          data[key] = target[tranfKey]
        }
        const allAdress = data.address.split('-')
        data.address = allAdress[0]
        data.street = allAdress[1]
        return data
      },
      // 跳转到虚拟卡兑换页
      navigateToV() {
        if(this.type === 'virtual' ) {
          this.$router.push({name: 'virtualCard', params: this.prizeInfo})
        }
      }
    },
    created() {
      this.init()
      clog('天天抽奖', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'领取奖品页','领取奖品页']);
    }
  }
</script>

