<template>
  <div class="form-wrap">
    <div class="form-box wrap-shadow">
      <template v-for="(item, index) in listData">
        <InputM class="input-item px-input-item"
                v-if="item.key!=='validate'"
                v-model="formData[item.key]"
                :icon="item.img"
                :placeholder="item.placeholder"
                :fontSize="30"
                :key="index"/>
        <template v-else>
          <div class="validate-code flex px-validate-code" v-if="!hasGet">
            <InputM class="input-item-validate"
                    v-model="formData[item.key]"
                    :icon="item.img"
                    :placeholder="item.placeholder"
                    :fontSize="30"
                    :key="index"/>
            <span @click="getValiCode" v-if="counter=== null" class="get-code">获取验证码</span>
            <span class="time-cont" v-else>{{counter + 's'}}</span>
          </div>
        </template>
      </template>
    </div>

    <ButtonM class="btn-ack" @click="ack" v-if="!hasGet">确定</ButtonM>
    <PrizePhoneNum></PrizePhoneNum>
    <div class="tag-wrap" v-if="hasGet">
      <five-tag class="five-tag">已领奖</five-tag>
    </div>
  </div>
</template>

<script>
  import InputM from '../../../../components/InputM/InputM';
  import ButtonM from '../../../../components/ButtonM/ButtonM';
  import FiveTag from '../FiveTag/FiveTag';
  import PrizePhoneNum from '../PrizePhoneNum/PrizePhoneNum';
  import {title_tip} from "../../../../utils/publictip";
  import {isPhoneNum} from "../../../../utils/tools";
  import {getCaptcha} from "../../../../api/my";
  const LIST = [
    {
      key: 'name',
      placeholder: '请输入收货人姓名',
      img: require('./imgs/icon-person.png')
    },
    {
      key: 'phone',
      placeholder: '请输入收货人手机号',
      img: require('./imgs/icon-phone.png')
    },
    {
      key: 'address',
      placeholder: '省市县/区、乡镇等',
      img: require('./imgs/icon-location.png')
    },
    {
      key: 'street',
      placeholder: '街道、楼牌号等',
      img: require('./imgs/icon-home.png')
    },
    {
      key: 'validate',
      placeholder: '请输入验证码',
      img: require('./imgs/icon-valicode.png')
    }
  ]
  const TYPE = {
    real: ['name', 'phone', 'address', 'street', 'validate'],
    virtual: ['name', 'phone', 'validate'],
    backShow: 'backShow'
  }
  const TIP = {
    name: '请输入收货人姓名',
    phone: '请输入收货人手机号',
    address: '省市县/区、乡镇等',
    street: '街道、楼牌号等',
    validate: '请输入验证码'
  }
  export default {
    name: "form-box",
    components: {InputM, ButtonM, FiveTag, PrizePhoneNum},
    props: {
      type: {
        type: String,
        default: 'virtual'  //'real'实物奖 'virtual'虚拟 'backShow'已领回显
      },
      hasGet: {
        type: Boolean,
        default: false  //'real'实物奖 'virtual'虚拟 'backShow'已领回显
      },
      showData: {
        type: Object,
        default: null
      }
    },
    data() {
      return {
        formData: {},
        listData: [],
        counter: null
      }
    },
    methods: {
      init() {
        const typeArr = TYPE[this.type]
        if(this.type === 'real') {
          this.listData = LIST
        } else if(this.type === 'virtual'){
          this.listData = LIST.filter(item => ~typeArr.indexOf(item.key))
        }
        if(this.hasGet) {
          this.listData.map(item => this.$set(this.formData, item.key, this.showData[item.key]))
        } else {
          this.listData.map(item => this.$set(this.formData, item.key, ''))
        }
      },
      getValiCode() {
        const needkeys = TYPE[this.type].slice()
        needkeys.pop()
        for(let i=0; i<needkeys.length; i++) {
          const curKey = needkeys[i]
          // console.log(curKey,this.formData[curKey], !this.formData[curKey])
          if(!this.formData[curKey]) {
            title_tip('提 示', TIP[curKey], '我知道了');
            return
          }
        }
        if(this.formData.phone && isPhoneNum(this.formData.phone)) {
          this.timeCounter()
          getCaptcha({
            phonenum: this.formData.phone
          })
            .then(res => {
              console.log(res);
            })
          return
        }
        title_tip('提 示', '请填写正确的手机号！~', '我知道了');
      },
      timeCounter(time) {
        this.counter = time || 60
        this.timer = setInterval(() => {
          if(this.counter <= 0) {
            clearInterval(this.timer)
            this.counter = null
            return
          }
          this.counter--
        }, 1000)
      },
      ack() {
        const needkeys = TYPE[this.type]
        for(let i=0; i<needkeys.length; i++) {
          const curKey = needkeys[i]
          if(!this.formData[curKey]) {
            title_tip('提 示', TIP[curKey], '我知道了');
            return
          }
        }
        if(this.formData.phone && !isPhoneNum(this.formData.phone)) {
          title_tip('提 示', '请填写正确的手机号！~', '我知道了');
          return
        }
        this.$emit('finish', Object.assign({}, this.formData))
      },
    },
    created() {
      this.init()
    }
  }
</script>

